import React from "react";
import Footer from "../components/footer.tsx";
import maintenance from "../assets/mountain_landscape.png";

const InMaintenancePage = () => {

    // CSS
    const bodyStyle: React.CSSProperties = {
        height: '80vh',
        position: 'relative', // Assure que les lments enfants soient positionns relativement  celui-ci
        backgroundImage: `url(${maintenance})`,
        backgroundSize: 'cover', 
        backgroundPosition: 'center', 
        //filter: 'blur(4px)', // Applique le flou  l'arrire-plan seulement
    };

    const divStyle: React.CSSProperties = {
        position: 'absolute', // Positionne le texte par rapport  son conteneur parent
        top: '50%', // Place le texte au milieu de son conteneur parent verticalement
        left: '50%', // Place le texte au milieu de son conteneur parent horizontalement
        transform: 'translate(-50%, -50%)', // Centre le texte
        backdropFilter: 'blur(4px)', // Applique le flou au texte seulement
        width: '100%',
        height: '100%', 
    };

    const textStyle: React.CSSProperties = {
        color: 'white',
        textAlign: 'center',
        lineHeight: '50vh',
        fontFamily: 'helvetica',
        fontSize: '36px',
    };

    return (
        <>
            <body style={bodyStyle}>
                <div style={divStyle}>
                    <h2 style={textStyle}>Page en maintenance</h2>                 
                    <p style={{color: 'white', textAlign: 'center', fontSize: '30px', marginTop: '-40px', fontFamily: 'Trebuchet MS'}}> Navr de la gne occasionne</p>
                    <p style={{fontFamily: 'Brush Script MT', color: 'white', fontSize: '30px', textAlign: 'center', paddingTop: '25px'}}> Bientt de retour !</p>
                </div>
            </body>
            <footer>
                <Footer />
            </footer>
        </>
    )
}

export default InMaintenancePage;
